import { Component, Inject, Input, ViewEncapsulation, OnInit, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';

import { ProjectsService } from '../projects.service';
import { Project } from '../project.model';

@Component({
    selector: 'fuse-project-form',
    templateUrl: './project-form.component.html',
    styleUrls: ['./project-form.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class FuseProjectFormComponent implements OnInit {
    action: string;
    dialogTitle: string;
    project: Project;
    projectForm: FormGroup;

    status = ['Draft', 'In review'];
    priorities = ['Low', 'Medium', 'High'];

    constructor(
        private formBuilder: FormBuilder,
        private projectsService: ProjectsService,
        @Inject(MAT_DIALOG_DATA) private data: any,
        public dialogRef: MatDialogRef<FuseProjectFormComponent>,
    ) {
        this.action = data.action;

        if ( this.action === 'edit' ) {
            this.dialogTitle = 'Edit Project';
            this.project = data.project;
        } else {
            this.dialogTitle = 'New Project';
            this.project = new Project({});
        }
    }

    ngOnInit() {
        this.projectForm = this.formBuilder.group({
            name       : [this.project.name],
            startDate  : [this.project.startDate],
            endDate    : [this.project.endDate],
            status     : [this.project.status],
            priority   : [this.project.priority],
            salesRep   : [this.project.salesRep],
            description: [this.project.description],
        });
    }

    save(form) {
        this.dialogRef.close([
            'save',
            {
                ...this.project,
                ...form.getRawValue()
            }
        ]);
    }

    delete() {
        this.dialogRef.close([ 'delete' ]);
    }
}



