import { Component, OnDestroy, OnInit, TemplateRef, ViewChild, ViewEncapsulation } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { MatDialog, MatDialogRef, MatPaginator, MatSort } from '@angular/material';
import { DataSource } from '@angular/cdk/collections';
import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';

import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import { FuseProjectFormComponent } from '../project-form/project-form.component';

import { ProjectsService } from '../projects.service';
import { Project } from '../project.model';

import 'rxjs/add/observable/merge';

@Component({
    selector     : 'fuse-projects-list',
    templateUrl  : './projects-list.component.html',
    styleUrls    : ['./projects-list.component.scss'],
    encapsulation: ViewEncapsulation.None,
    animations   : fuseAnimations
})
export class FuseProjectsListComponent implements OnInit, OnDestroy
{
    @ViewChild(MatPaginator) paginator: MatPaginator;
    @ViewChild(MatSort) sort: MatSort;

    dataSource: ProjectDataSource | null;
    displayedColumns = ['checkbox', 'name', 'startDate', 'endDate', 'status', 'salesRep', 'buttons'];
    checkboxes: {};
    selectedCount = 0;

    onStoresChangedSubscription: Subscription;
    onSelectionChangedSubscription: Subscription;

    confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
    dialogRef: MatDialogRef<FuseProjectFormComponent>;

    constructor(
        private projectsService: ProjectsService,
        private router: Router,
        public dialog: MatDialog
    )
    {
        this.onStoresChangedSubscription =
            this.projectsService.onStoresChanged.subscribe(projects => {
                this.checkboxes = {};
                projects.map(ws => {
                    this.checkboxes[ws.id] = false;
                });
            });

        this.onSelectionChangedSubscription =
            this.projectsService.onSelectionChanged.subscribe(selection => {
                for ( const id in this.checkboxes )
                {
                    if (!this.checkboxes.hasOwnProperty(id)) {
                        continue;
                    }

                    this.checkboxes[id] = selection.includes(id);
                }

                this.selectedCount = selection.length;
            });

    }

    ngOnInit()
    {
        this.dataSource = new ProjectDataSource(
            this.projectsService,
            this.paginator,
            this.sort
        );
    }

    ngOnDestroy()
    {
        this.onStoresChangedSubscription.unsubscribe();
        this.onSelectionChangedSubscription.unsubscribe();
    }

    deleteProject(ws)
    {
        this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
            disableClose: false
        });

        this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete?';

        this.confirmDialogRef.afterClosed().subscribe(result => {
            if ( result ) {
                this.projectsService.deleteProject(ws);
            }

            this.confirmDialogRef = null;
        });

    }

    onSelectedChange(wsId)
    {
        this.projectsService.toggleSelected(wsId);
    }

    editProject(project)
    {
        this.dialogRef = this.dialog.open(FuseProjectFormComponent, {
            panelClass: 'antera-details-dialog',
            data      : {
                action: 'edit',
                project
            }
        });

        this.dialogRef.afterClosed()
            .subscribe(response => {
                if ( !response )
                {
                    return;
                }

                const actionType: string = response[0];

                switch ( actionType )
                {
                    case 'save':
                        this.projectsService.updateProject(response[1]);
                        break;

                    case 'delete':
                        this.deleteProject(project);
                        break;
                }
            });
    }

    toggleAll(ev) {
        if (ev) {
            this.projectsService.toggleSelectAll();
        }
    }

    toggleEnable(ws, ev) {
        ev.stopPropagation();

        this.projectsService.updateProject({
            ...ws,
            enabled: !ws.enabled
        });
    }
}

export class ProjectDataSource extends DataSource<any>
{
    total = 0;

    private subscription: Subscription;

    constructor(
        private projectsService: ProjectsService,
        private _paginator: MatPaginator,
        private _sort: MatSort
    ) {
        super();
    }

    /** Connect function called by the table to retrieve one stream containing the data to render. */
    connect(): Observable<Project[]>
    {
        this.subscription = this.projectsService.onStoresChanged
            .subscribe(projects => {
                setTimeout(() => {
                    this.total = projects.length;
                });
            });

        const displayDataChanges = [
            this.projectsService.onStoresChanged,
            this._paginator.page,
            this._sort.sortChange
        ];

        return Observable.merge(...displayDataChanges)
            .map(() => {
                let projects = this.projectsService.projects;

                projects = this.sortData(projects);

                const startIndex = this._paginator.pageIndex * this._paginator.pageSize;
                projects = projects.slice(startIndex, startIndex + this._paginator.pageSize);

                return projects;
            });
    }

    sortData(data): Project[] {
        if ( !this._sort.active || this._sort.direction === '' )
        {
            return data;
        }

        return data.sort((a, b) => {
            let valueA = a[this._sort.active];
            let valueB = b[this._sort.active];

            return (valueA < valueB ? -1 : 1) * (this._sort.direction === 'asc' ? 1 : -1);
        });
    }

    disconnect()
    {
        this.subscription.unsubscribe();
    }
}



